
.wrapper {
  background-color: transparent;
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 99999;
}
.wrapper .play-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  box-sizing: border-box;
  width: 100%;
  height: 60px;
  border: 1px solid rgba(7,17,27,0.2);
}
.wrapper .play-wrapper .cover {
  width: 60px;
  height: 60px;
}
.wrapper .play-wrapper .cover>img {
  width: 100%;
  height: 100%;
}
.wrapper .play-wrapper .cover .mask {
  position: absolute;
  left: 0;
  top: 0;
  width: 60px;
  height: 60px;
  line-height: 60px;
  font-size: 25px;
  color: #fff;
  background-color: rgba(7,17,27,0.5);
  opacity: 0;
}
.wrapper .play-wrapper .cover:hover .mask {
  opacity: 1;
  transition: all 0.5s;
}
.wrapper .play-wrapper .detail {
  box-sizing: border-box;
  width: 100%;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.wrapper .play-wrapper .detail .ply {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding-left: 15px;
  padding-top: 14px;
  overflow: hidden;
}
.wrapper .play-wrapper .detail .ply .preSongBtn,
.wrapper .play-wrapper .detail .ply .nextSongBtn,
.wrapper .play-wrapper .detail .ply .ply-btn {
  float: left;
  width: 32px;
  height: 32px;
  background-color: #df3b3b;
}
.wrapper .play-wrapper .detail .ply .preSongBtn>i,
.wrapper .play-wrapper .detail .ply .nextSongBtn>i,
.wrapper .play-wrapper .detail .ply .ply-btn>i {
  color: #fff;
  font-size: 15px;
}
.wrapper .play-wrapper .detail .ply .nextSongBtn,
.wrapper .play-wrapper .detail .ply .ply-btn {
  margin-left: 20px;
}
.wrapper .play-wrapper .detail .controls {
  -webkit-box-flex: 4;
      -ms-flex: 4;
          flex: 4;
  box-sizing: border-box;
  text-align: left;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.wrapper .play-wrapper .detail .controls .top {
  height: 30px;
  line-height: 30px;
  font-size: 12px;
}
.wrapper .play-wrapper .detail .controls .top .bPlayingInfo {
  float: left;
}
.wrapper .play-wrapper .detail .controls .top .time {
  float: right;
  marging-right: 30px;
}
.wrapper .play-wrapper .detail .controls .bottom {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 30px;
}
.wrapper .play-wrapper .detail .controls .bottom .progressBar {
  -webkit-box-flex: 3;
      -ms-flex: 3;
          flex: 3;
  height: 4px;
  margin-top: 10px;
  border-radius: 10px;
  background-color: #e5e5e5;
  cursor: pointer;
}
.wrapper .play-wrapper .detail .controls .bottom .progressBar .progress {
  display: block;
  width: 0;
  height: 6px;
  background-color: #df3b3b;
  border-radius: 6px;
}
.wrapper .play-wrapper .detail .controls .bottom .volLine {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-left: 30px;
  cursor: pointer;
}
.wrapper .play-wrapper .detail .controls .bottom .volLine .noVol {
  width: 30px;
  height: 30px;
}
.wrapper .play-wrapper .detail .controls .bottom .volLine .vol {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 6px;
  margin-top: 10px;
  background-color: #e5e5e5;
  border-radius: 6px;
}
.wrapper .play-wrapper .detail .controls .bottom .volLine .vol .nowVol {
  display: block;
  width: 50%;
  height: 4px;
  background-color: #df3b3b;
  border-radius: 4px;
}
.wrapper .play-wrapper .detail .mode {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.wrapper .play-wrapper .detail .mode>span {
  display: block;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  color: #999;
  line-height: 60px;
  font-size: 15px;
  text-align: center;
  cursor: pointer;
}
/* 可以设置不同的进入和离开动画 */
/* 设置持续时间和动画函数 */
.slide-fade-enter-active {
  transition: all 0.3s ease;
}
.slide-fade-leave-active {
  transition: all 0.3s ease;
}
.slide-fade-enter,
.slide-fade-leave-to {
  -webkit-transform: scale(0.9, 0.9);
          transform: scale(0.9, 0.9);
  opacity: 0;
}


